body{
  background-image: linear-gradient(0deg, #00ffe7, pink,transparent);
  background-repeat:repeat-y;
  background-size: cover;
  height: 100vh;
  overflow-y: hidden;
}

.Heading{
  text-align: center;
  text-shadow: 5px 2px aqua;
}

.Class-Heading{
  position: absolute; left: 60%; top: 30%;
  font-size: larger;
  border: 2px solid black;
  border-radius: 20px;
  padding: 15px;
  height: 20px; width: 350px;
  background-color: aqua;
  text-align: center;
  cursor: pointer;
}

.Class-Container{
    border: 2px solid black;
    background-color: violet;
    height: 190px ; width: 350px;
    padding: 15px; 
    border-radius: 15px;
    position: absolute; left: 60%; top: 50%;
    line-height: 28px;
}

.External-Css{
  text-align: center;
}

.Function-Heading{
  position: absolute; top: 30%; left: 10%;
  font-size: larger;
  border: 2px solid black;
  border-radius: 20px;
  padding: 15px;
  height: 20px; width: 350px;
  background-color: aqua;
  text-align: center;
  cursor: pointer;
}

.Function-Container{
  border: 2px solid black;
  background-color: violet;
  height: 190px ; width: 350px;
  padding: 15px; 
  border-radius: 15px;
  position: absolute; left: 10%; top: 50%;
  line-height: 28px;
}
